.card {
  border-radius: 10px;
  height: 340px;
  cursor: pointer;
  position: relative;

  @media screen and (max-width: 1023px) {
    height: 297px;
  }

  @media screen and (max-width: 767px) {
    height: 255px;
  }

  &:hover {
    box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.5);
  }

  .card-overlay {
    position: relative;
    height: 100%;
    width: 100%;
    border-radius: 10px;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
      border-radius: 10px;
    }

    img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }

  .card-title-container {
    position: absolute;
    bottom: 10px;
    padding: 0 10px;

    h2 {
      color: white;
      font-size: 18px;
      font-weight: 500;
    }

  }

}

